import PropTypes from "prop-types";
import React from "react";
import "./style.css";

export const FormButton = ({ state, className, text = "LABEL" }) => {
  return (
    <div className={`form-button state-${state} ${className}`}>
      <div className="LABEL-2">{text}</div>
    </div>
  );
};

FormButton.propTypes = {
  state: PropTypes.oneOf(["hovered", "default"]),
  text: PropTypes.string,
};
