import PropTypes from "prop-types";
import React from "react";
import "./style.css";

export const NavbarButton = ({ property1, className, text = "LABEL" }) => {
  return (
    <div className={`navbar-button ${className}`}>
      <div className={`LABEL ${property1}`}>{text}</div>
    </div>
  );
};

NavbarButton.propTypes = {
  property1: PropTypes.oneOf(["hovered", "default"]),
  text: PropTypes.string,
};
